<template>
    <view class="page">
        <m-common title="弹窗" />
        <m-cell-group border>
            <m-cell
                title="基本使用"
                is-link
                @click="handelClick(0)"
            />
            <m-cell
                title="顶部弹出"
                is-link
                @click="handelClick(1)"
            />
            <m-cell
                title="右侧弹出"
                is-link
                @click="handelClick(2)"
            />
            <m-cell
                title="左侧弹出"
                is-link
                @click="handelClick(3)"
            />
            <m-cell
                title="居中弹出"
                is-link
                @click="handelClick(4)"
            />
            <m-cell
                title="显示圆角"
                is-link
                @click="handelClick(5)"
            />
            <m-cell
                title="禁止点击遮罩关闭"
                is-link
                @click="handelClick(6)"
            />
            <m-cell
                title="显示关闭按钮"
                is-link
                @click="handelClick(7)"
            />
        </m-cell-group>

        <m-popup
            :show="show"
            :mode="mode"
            :round="round"
            :closeable="flg2"
            :close-on-click-overlay="flg"
            @close="handleClose"
        >
            <view class="box">
                <text
                    class="btn"
                    @click="handleClose"
                >
                    关闭
                </text>
            </view>
        </m-popup>
    </view>
</template>

<script lang="ts" setup>
import { ref } from 'vue'

const show = ref(false)
const flg = ref(false)
const flg2 = ref(false)
const mode = ref('bottom')
const round = ref(0)

const handelClick = index => {
    const obj = {
        0: 'bottom',
        1: 'top',
        2: 'right',
        3: 'left',
        4: 'center',
        5: 'bottom',
        6: 'bottom',
        7: 'bottom',
    }
    mode.value = obj[index]
    show.value = true
    round.value = index === 5 ? 10 : 0
    flg.value = index !== 6
    flg2.value = index === 7
}

const handleClose = () => {
    show.value = false
}
</script>

<style lang="scss" scoped>
.page {
    .box {
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 50px;
    }

    .btn {
        background-color: $m-primary;
        color: white;
        padding: 10px 20px;
        border-radius: 4px;
    }
}
</style>
